.converter-wrapper {
    height: 100vh;
    background: #030515;
    color: #FFFFFF;
    padding: 10px;
}

.app-title {
    font-size: 30px;
    font-weight: 800;
    margin-bottom: 10px;
    text-align: center;
}

.app-title-detail {
    color: #fe4c27;
}

.convertor-text {
    column-width: 60px;
    font-weight: 600;
    margin-right: 10px;
}

.convertor-input {
    width: 60px;
    border: none;
    padding: 5px;
    border-radius: 10px;
    outline: none;
}

.convertor-input:active, .convertor-input:focus {
    outline: none;
}

.convertor-row {
    margin-bottom: 10px;
}

.converter-result {
    min-width: 100px;
    height: 50px;
    background: #fe4c27;
    font-size: 25px;
    text-align: center;
    padding: 10px;
    font-weight: 900;
    border-radius: 50px;
}

